;\n  @include for-tablet-up {\n    margin-left: 26px;\n  }\n  @include for-phone-up {\n    margin-left: 0;\n  }\n}\n\n// logo rounds\n.logo::before,\n.logo::after {\n  content: '';\n  position: absolute;\n  top: -5px;\n  left: 0;\n  width: 62px;\n  height: 62px;\n  display: block;\n  z-index: -1;\n  border: 1px solid $white;\n  border-radius: 50%;\n  opacity: 0.5;\n}\n\n.logo::after {\n  left: 30px;\n}\n\n//logo dots\n.logo__round {\n  position: absolute;\n  z-index: 1;\n  font-family: 'Josefin Sans', sans-serif;\n  font-size: 10px;\n  text-align: center;\n  color: $white-trans-5;\n  -webkit-animation: round-rotate 4s linear infinite;\n  animation: round-rotate 4s linear infinite;\n\n  &_left {\n    top: 1px;\n    left: 6px;\n    -webkit-transform-origin: 26px 26px;\n    transform-origin: 26px 26px;\n  }\n\n  &_right {\n    top: 48px;\n    left: 43px;\n    -webkit-transform-origin: -12px -22px;\n    transform-origin: -12px -22px;\n  }\n}\n\n//dots background\n.logo__round::before {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 8px;\n  height: 8px;\n  z-index: -1;\n  border-radius: 50%;\n  background: $black;\n}\n\n@-webkit-keyframes round-rotate {\n  to {\n    -webkit-transform: rotate(1turn);\n  }\n}\n\n@keyframes round-rotate {\n  to {\n    transform: rotate(1turn);\n  }\n}\n\n.logo__title {\n  z-index: 2;\n  font-weight: bold;\n  font-size: 2.7rem;\n  letter-spacing: -0.1rem;\n}\n\n// * navbar\n\n.nav {\n  width: calc(100% - 120px - 166px);\n  @include flex-between;\n  @include for-tablet-up {\n    width: calc(100% - 115px - 166px);\n  }\n  @include for-tablet-down {\n    width: calc(100% - 60px - 166px);\n  }\n  @include for-phone-up {\n    width: 54px;\n  }\n}\n\n.nav__list {\n  @include flex-between;\n  @include for-phone-up {\n    padding-bottom: 15px;\n    width: 100vw;\n    height: 75px;\n    position: fixed;\n    bottom: 0;\n    left: 0;\n    justify-content: space-around;\n    border: 1px solid $white;\n    border-radius: 40px 40px 0px 0px;\n    background-color: $black;\n  }\n  @include for-phone {\n    border-radius: 20px 20px 0px 0px;\n  }\n}\n\n.nav__item {\n  margin-right: 52px;\n  @include for-tablet-up {\n    margin-right: 22px;\n  }\n  @include for-phone-up {\n    width: 28px;\n    height: 28px;\n    margin-right: 0;\n    @include flex-column-center;\n  }\n}\n\n.nav__link {\n  padding: 20px;\n  font-size: 1.3rem;\n  @include for-tablet-up {\n    padding: 30px 10px;\n    font-size: 1.1rem;\n  }\n  @include for-phone-up {\n    margin-top: 20px;\n    padding: 10px;\n    font-size: 0.8rem;\n  }\n}\n\n// TODO: active item\n.categories-page .nav__categories .nav__link {\n  font-weight: 700;\n  color: $general;\n  @include for-phone-up {\n    font-weight: 500;\n  }\n}\n\n.categories-page .nav__categories {\n  @include for-phone-up {\n    background-image: url('../assets/svg/categories-color.svg');\n  }\n}\n// TODO\n\n.nav__categories {\n  @include for-phone-up {\n    background-image: url('../assets/svg/categories.svg');\n  }\n}\n\n.nav__home {\n  @include for-phone-up {\n    background-image: url('../assets/svg/home.svg');\n  }\n}\n\n.nav__score {\n  @include for-phone-up {\n    background-image: url('../assets/svg/score.svg');\n  }\n}\n\n.settings__button {\n  padding: 20px;\n  @include for-tablet-up {\n    padding: 10px;\n  }\n}\n\n.settings__icon {\n  width: 36px;\n  height: 36px;\n  background-image: url('../assets/svg/settings.svg');\n  @include for-tablet-up {\n    width: 34px;\n    height: 34px;\n  }\n}\n\n.settings__link:hover .settings__icon {\n  background-image: url('../assets/svg/settings-color.svg');\n}\n\n.close__button {\n  width: 36px;\n  height: 36px;\n  position: relative;\n  @include for-phone {\n    display: none;\n  }\n}\n\n.close__button::before,\n.close__button::after {\n  content: '';\n  position: absolute;\n  top: 50%;\n  left: 5px;\n  height: 3px;\n  width: 26px;\n  display: block;\n  transform: rotate(45deg);\n  background-color: $white;\n  border-radius: 30px;\